<template>
  <div>
    <!--  -->
    <config-item v-show="mValue.model == 'custom'" label="">
      <el-select
        v-model="mValue.row"
        placeholder=""
        size="small"
        @change="changeRow"
      >
        <el-option
          v-for="key in 10"
          :key="key"
          :label="key + ''"
          :value="key"
        >
        </el-option>
      </el-select>
    </config-item>

    <!--  -->
    <template>
      <div class="f-red f13 mb15 pl10 pr10"></div>
      <div class="f-grey f12 mb15 pl10 pr10"></div>
      <div class="pl10 pl10">
        <cap-cube-layout
          ref="layout"
          :row="mValue.row"
          :model="mValue.model"
          v-model="mValue.list"
          @onCuurIndex="onCuurIndex"
        ></cap-cube-layout>
      </div>
    </template>

    <!--  -->
    <SchemaSelect
      class="mt15 mb20"
      label=""
      v-model="mValue.model"
      :data="modelOptions"
      @change="changeModel"
    >
    </SchemaSelect>

    <div v-if="activeItem" class="nav ml10 mr10 mb20">
      {{ activeItem }}
      <SchemaUpload label="" v-model="activeItem.image" />
      <SchemaJump label="" v-model="activeItem.jump"></SchemaJump>
    </div>
  </div>
</template>

<script>
import schemaMixin from "@/mixin/schemaMixin";
import CapCubeLayout from "./CapCubeLayout";
import { initialModels, modelOptions } from "./config";

export default {
  name: "SchemaCapCube",

  mixins: [schemaMixin],

  components: {
    CapCubeLayout,
  },

  props: {
    value: {
      type: Object,
      default: () => {},
    },
  },

  data() {
    return {
      modelOptions,
      activeItem: null,
    };
  },

  methods: {
    // 
    changeModel(model) {
      if (model) {
        // 
        let target = this.modelOptions.find((m) => m.value == model);
        this.$set(this.mValue, "row", target.row);

        // 
        this.$refs.layout.reset();

        // 
        if (model == "custom") {
          this.$set(this.mValue, "list", []);
        } else {
          this.$set(
            this.mValue,
            "list",
            this._.cloneDeep(initialModels[model])
          );
        }
      }
    },

    // 
    changeRow() {
      this.$refs.layout.reset();
    },

    //  
    onCuurIndex(item) {
      this.activeItem = null;
      setTimeout(() => {
        this.activeItem = item;
      }, 10);
    },
  },
};
</script>
