import Vue from 'vue'

export default function jump(data) {
    console.log(data);


    let { type, id } = data
    console.log(type);
    console.log(type);



    // 
    if (type == 'custom') {
        if (!id) {
            console.warn('id');
            return
        }
        
        this.$router.push({ name: 'custom', query: { pageId: id } })
    }

    // 
    if (type == 'fixed') {
        if (!id) {
            console.warn('id');
            return
        }
        this.$router.push({ name: data.id })
    }

    // 
    if (type == 'goods') {
        this.$router.push({ name: 'goods-detail', query: { id } })
    }
}
